.container {
 margin-left: auto;
 margin-right: auto;
 max-width: 56rem;
 padding: 1rem; /* reduced from p-6 */
    background: linear-gradient(135deg, #f0f7ff 0%, #f5f3ff 100%);
}
  
  .card {
 border-width: 0px;
 --tw-bg-opacity: 1;
 background-color: rgb(255 255 255 / var(--tw-bg-opacity));
 --tw-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
 --tw-shadow-colored: 0 20px 25px -5px var(--tw-shadow-color), 0 8px 10px -6px var(--tw-shadow-color);
 box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
  
  .header {
 border-top-left-radius: 0.5rem;
 border-top-right-radius: 0.5rem;
 background-image: linear-gradient(to right, var(--tw-gradient-stops));
 --tw-gradient-from: #2563eb var(--tw-gradient-from-position);
 --tw-gradient-to: rgb(37 99 235 / 0) var(--tw-gradient-to-position);
 --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
 --tw-gradient-to: #9333ea var(--tw-gradient-to-position);
 padding: 1.5rem;
 --tw-text-opacity: 1;
 color: rgb(255 255 255 / var(--tw-text-opacity));
}
  
  .headerContent {
 display: flex;
 align-items: center;
 justify-content: center;
}
  
  .headerContent > :not([hidden]) ~ :not([hidden]) {
 --tw-space-x-reverse: 0;
 margin-right: calc(0.75rem * var(--tw-space-x-reverse));
 margin-left: calc(0.75rem * calc(1 - var(--tw-space-x-reverse)));
}
  
  .title {
 font-size: 1.875rem;
 line-height: 2.25rem;
 font-weight: 700;
 --tw-text-opacity: 1;
 color: rgb(255 255 255 / var(--tw-text-opacity));
}
  
  .locationSelector {
 margin-bottom: 2rem;
}
  
  .locationSelector > :not([hidden]) ~ :not([hidden]) {
 --tw-space-y-reverse: 0;
 margin-top: calc(0.5rem * calc(1 - var(--tw-space-y-reverse)));
 margin-bottom: calc(0.5rem * var(--tw-space-y-reverse));
}
  
  .selectorLabel {
 display: flex;
 align-items: center;
}
  
  .selectorLabel > :not([hidden]) ~ :not([hidden]) {
 --tw-space-x-reverse: 0;
 margin-right: calc(0.5rem * var(--tw-space-x-reverse));
 margin-left: calc(0.5rem * calc(1 - var(--tw-space-x-reverse)));
}
  
  .selectorLabel {
 font-size: 0.875rem;
 line-height: 1.25rem;
 font-weight: 600;
 --tw-text-opacity: 1;
 color: rgb(55 65 81 / var(--tw-text-opacity));
}
  
  .selectTrigger {
 width: 100%;
 border-width: 2px;
 transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
 transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
 transition-duration: 200ms;
 animation-duration: 200ms;
}
  
  .selectTrigger:hover {
 --tw-border-opacity: 1;
 border-color: rgb(96 165 250 / var(--tw-border-opacity));
}
  
  @media (min-width: 768px) {
  
 .selectTrigger {
  width: 16rem;
 }
}
  
  .gridHeader {
 margin-bottom: 1rem;
 display: grid;
 grid-template-columns: repeat(4, minmax(0, 1fr));
 gap: 1rem;
 border-radius: 0.5rem;
 --tw-bg-opacity: 1;
 background-color: rgb(243 244 246 / var(--tw-bg-opacity));
 padding-left: 1rem;
 padding-right: 1rem;
 padding-top: 0.5rem;
 padding-bottom: 0.5rem;
 font-weight: 600;
 --tw-text-opacity: 1;
 color: rgb(55 65 81 / var(--tw-text-opacity));
}
  
  .columnHeader {
 display: flex;
 align-items: center;
 justify-content: center;
}
  
  .taskRow {
 display: grid;
 grid-template-columns: repeat(4, minmax(0, 1fr));
 align-items: center;
 gap: 1rem;
 border-radius: 0.5rem;
 padding: 0.5rem;
 transition-property: all;
 transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
 transition-duration: 200ms;
 animation-duration: 200ms; /* reduced from p-4 */
}
  
  .taskRowActive {
 --tw-bg-opacity: 1;
 background-color: rgb(255 255 255 / var(--tw-bg-opacity));
 --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
 --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
 box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
  
  .taskRowInactive {
 --tw-bg-opacity: 1;
 background-color: rgb(249 250 251 / var(--tw-bg-opacity));
}
  
  .taskLabel {
 grid-column: span 1 / span 1;
 display: flex;
 align-items: center;
}
  
  .taskLabel > :not([hidden]) ~ :not([hidden]) {
 --tw-space-x-reverse: 0;
 margin-right: calc(0.75rem * var(--tw-space-x-reverse));
 margin-left: calc(0.75rem * calc(1 - var(--tw-space-x-reverse)));
}
  
  .checkbox {
 height: 1.25rem;
 width: 1.25rem;
 border-width: 2px;
}
  
  .checkbox[data-state="checked"] {
 --tw-bg-opacity: 1;
 background-color: rgb(37 99 235 / var(--tw-bg-opacity));
}
  
  .inputField {
 margin-left: auto;
 margin-right: auto;
 width: 6rem;
 border-width: 2px;
 text-align: center;
 transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
 transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
 transition-duration: 200ms;
 animation-duration: 200ms;
}
  
  .inputField:hover {
 --tw-border-opacity: 1;
 border-color: rgb(96 165 250 / var(--tw-border-opacity));
}
  
  .infoIcon {
 margin-left: 0.5rem;
 display: inline-block;
 height: 1rem;
 width: 1rem;
 cursor: help;
 --tw-text-opacity: 1;
 color: rgb(96 165 250 / var(--tw-text-opacity));
 transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
 transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
 transition-duration: 200ms;
 animation-duration: 200ms;
}
  
  .infoIcon:hover {
 --tw-text-opacity: 1;
 color: rgb(37 99 235 / var(--tw-text-opacity));
}
  
  .totalSection {
 margin-top: 2rem;
}
  
  .totalCard {
 transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
 border-radius: 0.5rem;
 background-image: linear-gradient(to right, var(--tw-gradient-stops));
 --tw-gradient-from: #2563eb var(--tw-gradient-from-position);
 --tw-gradient-to: rgb(37 99 235 / 0) var(--tw-gradient-to-position);
 --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
 --tw-gradient-to: #9333ea var(--tw-gradient-to-position);
 padding: 1.5rem;
 --tw-text-opacity: 1;
 color: rgb(255 255 255 / var(--tw-text-opacity));
 --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
 --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
 box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
 transition-property: all;
 transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
 transition-duration: 200ms;
 animation-duration: 200ms;
}
  
  .totalCard:hover {
 --tw-scale-x: 1.05;
 --tw-scale-y: 1.05;
 transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}
  
  .totalValue {
 text-align: center;
 font-size: 1.5rem;
 line-height: 2rem;
 font-weight: 700;
}
  
  .totalDescription {
 margin-top: 0.5rem;
 text-align: center;
 font-size: 0.875rem;
 line-height: 1.25rem;
 --tw-text-opacity: 1;
 color: rgb(219 234 254 / var(--tw-text-opacity));
}
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(4px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .fadeInAnimation {
    animation: fadeIn 0.3s ease-out;
  }
  
  /* Hover Effects */
  .inputField:focus {
 border-color: transparent;
 outline: 2px solid transparent;
 outline-offset: 2px;
 --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
 --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
 box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
 --tw-ring-opacity: 1;
 --tw-ring-color: rgb(96 165 250 / var(--tw-ring-opacity));
}
  
  .taskRow:hover {
 --tw-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
 --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
 box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .gridHeader {
  font-size: 0.875rem;
  line-height: 1.25rem;
 }
    
    .taskRow {
  font-size: 0.875rem;
  line-height: 1.25rem;
 }
    
    .inputField {
  width: 5rem;
 }
  }